import React from 'react';
import styles from './Navbar.module.scss';
import {NavLink} from 'react-router-dom';

class Navbar extends React.Component {
    state = {
        navbarClass : false,
    }
    componentDidMount = () => {
        setInterval(this.changeClass,100);
    }
    changeClass = () => {
        if(window.pageYOffset > 3){
            this.setState({navbarClass : true});
        } else {
            this.setState({navbarClass : false});
        }
    }
    render(){
        return(
            <>
                <ul className={this.state.navbarClass ? styles.nav2 : styles.nav}>
                    <li className={styles.logo}></li>
                    <li className={styles.listItem}><h2><NavLink className={styles.link} exact to="/section1">Wnętrze jednostki centralnej</NavLink></h2></li>
                    <li className={styles.listItem}><h2><NavLink className={styles.link} exact to="/section2">Urządzenia wyjścia</NavLink></h2></li>
                    <li className={styles.listItem}><h2><NavLink className={styles.link} exact to="/section3">Urządzenia wejścia</NavLink></h2></li>
                </ul>
            </>
        )
    }
}
export default Navbar;